				"html": "<p><strong><em>Concept</em></strong></p>\n<ul>\n<li>Can we achieve a great purpose by gathering small jobs?</li>\n<li>How can we achieve constructive collaboration?</li>\n<li>How should technology be related in order to increase human possibilities?</li>\n</ul>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>The core idea is to gather work capacity little by little for the big purpose.\nThis is an image that generalized the power of open source project.\nThe power of open source is wonderful, and most of IT-related people benefit from it.\nCan we apply this to a wider range of fields?</p>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>Some requested elements are shown below.</p>\n<br>\n<p><strong>▽ Organize the team</strong>\nFirst of all we need people to participate.\nPick up from registrant, or make offer to people in the network.\nFor example, AI selects the appropriate person.</p>\n<br>\n<p><strong>▽ Work and hobby crossing</strong></p>\n<p>They will be able to participate as an extension of enjoyment.\nIt is their goodness and it is fun for them.\nOr it may lead to another job opportunity as a result.</p>\n<br>\n<p><strong>▽ Connect quickly at low cost</strong></p>\n<p>Time, place, combination. Online or Offline.\nFor example, they will meet face to face using VR.\nOr we may think that we can achieve by combining individual result alone.\nIn other words without actually interacting.</p>\n<br>\n<p><strong>▽ Visualize the results</strong></p>\n<p>They will be able to get immediate feedback.\nAlso, it needs to be easy to understand.\nFor example, data visualization is also one way.</p>\n<br>\n<p><strong>▽ Automated process</strong></p>\n<p>Asynchronous, Cooperative behavior, Event driven, Reactive functions and else.</p>\n<br>\n<p><strong>▽ Urge emergence （創発）</strong></p>\n<p>Complex organization of a plurality of local interactions constitutes a system that can not be predicted from the behavior of individual element.</p>",
